import '@babel/polyfill';;
import Vue from 'vue/dist/vue.js'

new Vue({
	el: '#app',
	data: {
		scrolled: false,
		showModal: false,
		showMenu: false,
		showList: false
	},
	methods: {
		handleScroll () {
			this.scrolled = window.scrollY > 120;
		}
	},
	created () {
		window.addEventListener('scroll', this.handleScroll);
	},
	destroyed () {
		window.removeEventListener('scroll', this.handleScroll);
	}
});
