import Swiper from 'swiper/dist/js/swiper';

export default class News {
	constructor(parent) {
		const news = parent;
		const sliderNews = news.querySelector('.news__slider');

		new Swiper(sliderNews, {
			slidesPerView: 4,
			spaceBetween: 20,
			loop: true,
			grabCursor: true,
			navigation: {
				nextEl: '.news__arrow--next',
				prevEl: '.news__arrow--prev',
			},
			pagination: {
				el: '.swiper-pagination',
				type: 'bullets',
			},
			breakpoints: {
				768: {
					slidesPerView: 1,
				},
				1279: {
					slidesPerView: 2,
				},
				1440: {
					slidesPerView: 3,
				}
			}
		});
	}
};
