import Swiper from 'swiper/dist/js/swiper';

export default class MainSlider {
	constructor (parent) {
		const slider_img = parent.querySelector('.main-slider__img');
		const slider_content = parent.querySelector('.main-slider__content');

		this.slider_img =  new Swiper (slider_img, {
			loop: true,
			effect: 'fade',
			pagination: {
				el: '.main-slider__pagination',
				type: 'bullets',
				clickable: true
			}
		});

		this.slider_content = new Swiper (slider_content, {
			loop: true,
			grabCursor: true,
		});

		this.slider_content.controller.control = this.slider_img;
	}
};
