import 'svgxuse';
import './vendor';
import MainSlider from './main-slider';
import News from './news';

Array.from(document.querySelectorAll('.main-slider')).forEach(element => {
	if (element) {
		new MainSlider(element);
	}
});

Array.from(document.querySelectorAll('.news')).forEach(element => {
	if (element) {
		new News(element);
	}
});

